.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  font-family: 'Segoe UI', Arial, sans-serif;
  background: linear-gradient(to bottom, #f5f5f5, #eaeaea);
  color: #2c3e50;
}

main {
  flex: 1;
  display: flex;
  flex-direction: column;
}

/* Liens stylisés */
.App-link {
  color: #ffb347;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s;
}

.App-link:hover {
  color: #e67e22;
}

/* Boutons verticaux avec image */
.vertical-buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin: 32px 0;
  flex-wrap: wrap;
}

.vertical-buttons button {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding: 32px 12px;
  font-size: 18px;
  font-weight: 600;
  border-radius: 12px;
  border: none;
  background: linear-gradient(90deg, #2d3e50 60%, #4e5d6c 100%);
  color: #fff;
  box-shadow: 0 4px 12px rgba(44, 62, 80, 0.1);
  cursor: pointer;
  transition: background 0.2s, transform 0.15s, box-shadow 0.2s;
  outline: none;
}

.vertical-buttons button:hover,
.vertical-buttons button:focus {
  background: linear-gradient(90deg, #1a232e 60%, #2d3e50 100%);
  transform: translateY(-2px) scale(1.03);
  box-shadow: 0 6px 16px rgba(44, 62, 80, 0.2);
}

.vertical-buttons img {
  width: 140px;
  height: 140px;
  object-fit: cover;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.12);
  background: #fff;
}

/* Responsive */
@media (max-width: 700px) {
  .vertical-buttons {
    flex-direction: row;
    gap: 0.5rem;
  }

  .vertical-buttons button {
    width: 120px;
    max-width: 350px;
    font-size: 16px;
    padding: 16px 8px;
  }

   .vertical-buttons img {
    width: 120px;
    height: 120px;
  }
}
/* Bouton flottant WhatsApp */
.whatsapp-chatbot-btn {
  position: fixed;
  bottom: 25px;
  right: 24px;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #25D366;
  color: #000;
  border-radius: 50%;
  width: 56px;
  height: 56px;
  padding: 0;
  text-decoration: none;
  box-shadow: 0 2px 8px rgba(0,0,0,0.15);
  transition: background 0.2s, box-shadow 0.2s, transform 0.15s;
  border: none;
}

.whatsapp-chatbot-btn:hover,
.whatsapp-chatbot-btn:focus {
  background: #1ebe57;
  box-shadow: 0 4px 16px rgba(37,211,102,0.18);
  transform: translateY(-2px) scale(1.07);
  color: #fff;
  text-decoration: none;
}

.whatsapp-chatbot-btn svg {
  width: 32px;
  height: 32px;
  display: block;
  margin: 0;
}

/* Responsive pour mobile */
@media (max-width: 600px) {
  .whatsapp-chatbot-btn {
    width: 30px;
    height: 38px;
    bottom: 70px;
    right: 16px;
   
  }
  .whatsapp-chatbot-btn svg {
    width: 24px;
    height: 24px;
  }
}